

.firstsectionWrapper {
    background-image: url(../../resources/images/Blue-and-Orange-Wallpaper.jpg);
    @apply bg-cover bg-center bg-no-repeat bg-fixed w-full ;
}

.placing{
    @apply pl-28 p-80;
    @apply md:pl-0;   
}

.firstText{
    
    @apply text-white font-sans font-bold text-8xl;
    @apply sm:ml-20;
    @apply md:ml-20; 
    @apply lg:ml-20;
}

.secondText{
    @apply flex text-white font-sans mt-5 text-4xl;
    @apply sm:ml-20;
    @apply md:ml-20; 
    @apply lg:ml-20;
}
.secondsectionWrapper {
    @apply bg-neutral-800 ;
    @apply sm:p-32;
    @apply md:p-32;
    @apply lg:p-32;
}
.cardPosition{
    @apply bg-white p-20 rounded-3xl overflow-hidden relative px-28;
    /* @apply block p-20  shadow-lg  max-w-4xl px-28 py-20 mt-8 mb-8; */
}
.cardTitle{
    @apply text-gray-900 text-8xl leading-tight font-bold mb-2;
}

.cardSubTitle{
    @apply text-gray-900 text-5xl leading-tight font-semibold mb-2;
}

.cardText{
    @apply mt-8 text-2xl font-medium;
}
.cardButton{
    @apply outline outline-black px-6 py-2.5 mt-28 font-medium text-lg leading-tight shadow-md hover:shadow-lg 
    focus:shadow-lg  focus:ring-0 
    active:shadow-lg transition 
    duration-150 hover:bg-black hover:text-white 
    rounded-full ease-in-out;

}
.textSingleButton{
    @apply text-center flex w-11/12 justify-center font-bold text-4xl text-white font-sans mt-20 pt-10;
}
