.lifeEasyCard {
    @apply flex flex-col;
    @apply w-11/12  lg:min-w-[18rem];
    @apply bg-primary-white rounded-2xl;
}

.llifeEasyHeader {
    @apply w-full mb-2;
}
.headingText {
    @apply text-3xl font-black;
}

.llifeEasyBody {
    @apply w-full;
}

.text {
    @apply px-8 py-5;
}

.lifeEasyBodyText {
    @apply w-full font-semibold text-gray-500 text-left pt-3;
}
.imageDiv {
    @apply w-2/5 p-2 md:p-5 h-2/5;
}
.image {
    @apply scale-75 hover:scale-100;
}
