.wrapper{
    @apply h-fit xl:h-[650px] bg-primary-white p-12 py-12 w-full mx-auto my-12 rounded-2xl flex flex-col justify-around;
}

.container{
    @apply w-full h-full bg-primary-white p-0 md:p-4;
}

.top{
    @apply text-5xl md:text-7xl font-black w-1/3 md:my-5 mb-8;
}
.bottom{
    @apply lg:pb-24 text-lg;
}